import React, { PropTypes} from 'react';
import {Link, IndexLink} from 'react-router';

const Header = () => {
    return (
        <nav className="margin-botnav">
            <IndexLink to="/" activeClassName="active">Home</IndexLink>
        </nav>
    );
};

export default Header;


